import * as httpRequest from '~/untils/httpRequest'; //call API wtth axios

// export const controller = new AbortController();
export async function getSuggestedAccounts(page = 1, except = [], per_page) {
  try {
    const res = await httpRequest.get('users/suggested', {
      //ch  gi nguyn trng thi k t ca URL
      params: {
        // page: S trang. VD trang 1 ti 5 users th trang 2 s ti thm 5 users tip theo
        page,
        // except: Danh sch id s b loi tr khi response, phn cch cc id bng du phy. VD: 1,2,3,4
        except: except.join(),
        // per_page: S lng tr v trn mt trang
        per_page,
      },
      // signal: controller.signal,
    });

    return res.data;
  } catch (error) {
    console.log(error);
  }
}
export async function getFollowingAccounts(page = 1, except = []) {
  try {
    const res = await httpRequest.get('me/followings', {
      //ch  gi nguyn trng thi k t ca URL
      params: {
        // page: S trang. VD trang 1 ti 5 users th trang 2 s ti thm 5 users tip theo
        page,
        // except: Danh sch id s b loi tr khi response, phn cch cc id bng du phy. VD: 1,2,3,4
        except: except.join(),
      },
      // signal: controller.signal,
    });

    return res.data;
  } catch (error) {
    console.log(error);
  }
}
