import { Fragment } from 'react';
import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { publicRoutes } from '~/routes/index.js';
// import { DefaultLayout } from '~/layouts/DefaultLayout/index.js'; //kiu ny import cho newbie
import DefaultLayout from '~/layouts';

function App() {
  return (
    <Router basename={process.env.PUBLIC_URL}>
      <div className="App">
        <Routes>
          {publicRoutes.map((routes, index) => {
            const Page = routes.component; // v element nhn component c c php JSX,nn khng th nht trc tip router.component v c

            //kim tra nu pages c dng chung layout default khng?
            let Layout = DefaultLayout; //Mc nh th ng layout default
            if (routes.layout) {
              Layout = routes.layout; //hay s dng layout khc vd: HeaderOnly
            } else if (routes.layout === null) {
              Layout = Fragment; //Hoc khng c layout
            }

            return (
              <Route
                key={index}
                path={routes.path}
                element={
                  <Layout>
                    <Page />
                  </Layout>
                }
              />
            );
          })}
        </Routes>
      </div>
    </Router>
  );
}

export default App;
