import * as httpRequest from '~/untils/httpRequest'; //call API wtth axios

// export const controller = new AbortController();
export async function getVideos(type = 'for-you', page = '1', except = null) {
  try {
    const res = await httpRequest.get('videos', {
      //ch  gi nguyn trng thi k t ca URL
      params: {
        // type: "for-you" or "following". for-you: Ly danh sch posts dnh cho bn. following: Ly danh sch posts ca nhng ngi bn ang theo di
        type,
        // page: Dng  ti thm d liu posts. VD: Khi  trang 1 s ti 10 posts u, sang trang 2 s ti thm 10 posts tip theo
        page,
        // except: UID ca video s b loi tr khi kt qu
        except,
      },
      // signal: controller.signal,
    });

    return res.data;
  } catch (error) {
    console.log(error);
  }
}
